import { Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { ApiService } from '@core/services/api.service'

@Component({
  selector: 'app-category-breadcrumbs',
  templateUrl: './breadcrumbs.component.html',
  styleUrls: ['./breadcrumbs.component.scss']
})
export class BreadcrumbsComponent {

  @Input()
  public menuItems: { slug: string, title: string }[]

  @Input()
  public heading: string

  @Input()
  public subtitle: string

  public constructor(private router: Router, private route: ActivatedRoute, private api: ApiService) {
    this.route.params.subscribe(() => {
    })
  }

  changeRoute(slug: string) {
    if (this.route.snapshot.params['category'] != slug) {
      this.router.navigate([`/${slug}`])
    }
  }

}
