import { Injectable } from '@angular/core'
import { CollectionViewer, DataSource, SelectionChange } from '@angular/cdk/collections'
import { ProductCategory } from '@core/models/types'
import { BehaviorSubject, firstValueFrom, map, merge, Observable, of, tap } from 'rxjs'
import { FlatTreeControl } from '@angular/cdk/tree'
import { MatTreeFlattener } from '@angular/material/tree'
import { ApiService } from '@core/services/api.service'
import { HttpParams } from '@angular/common/http'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { DynamicFlatNode } from '../models/types'

@Injectable({
  providedIn: 'root',
})
export class DynamicTreeDataSourceService  implements DataSource<DynamicFlatNode<ProductCategory>> {
  private _cachedCategories: Record<string, Array<ProductCategory>> = {}
  private dataChange = new BehaviorSubject<DynamicFlatNode<ProductCategory>[]>([])
  private _hasMore = true
  private _loadingAll = false

  get data(): DynamicFlatNode<ProductCategory>[] {
    return this.dataChange.value
  }

  set data(value: DynamicFlatNode<ProductCategory>[]) {
    this._treeControl.dataNodes = value
    this.dataChange.next(value)
  }

  get hasMore() {
    return this._hasMore
  }

  get loadingAll() {
    return this._loadingAll
  }

  constructor(
    private _treeControl: FlatTreeControl<DynamicFlatNode<ProductCategory>>,
    private api: ApiService,
    private treeFlattener: MatTreeFlattener<any, any>
  ) {}

  connect(collectionViewer: CollectionViewer): Observable<DynamicFlatNode<ProductCategory>[]> {
    this._treeControl.expansionModel.changed.subscribe(change => {
      if (
        (change as SelectionChange<DynamicFlatNode<ProductCategory>>).added ||
        (change as SelectionChange<DynamicFlatNode<ProductCategory>>).removed
      ) {
        this.handleTreeControl(change as SelectionChange<DynamicFlatNode<ProductCategory>>)
      }
    })

    return merge(collectionViewer.viewChange, this.dataChange).pipe(map(() => this.data))
  }

  disconnect(collectionViewer: CollectionViewer): void {
    console.log(collectionViewer)
  }

  /** Handle expand/collapse behaviors */
  private handleTreeControl(change: SelectionChange<DynamicFlatNode<ProductCategory>>) {
    if (change.added) {
      change.added.forEach(node => this.toggleNode(node, true))
    }
    if (change.removed) {
      change.removed.slice().reverse().forEach(node => this.toggleNode(node, false))
    }
  }

  private findChildrenCount(index: number, level: number) {
    let count = 0
    let i = index + 1
    while (i < this.data.length && this.data[i].level > level) {
      count++
      i++
    }
    return count
  }

  private removeChildren(node: DynamicFlatNode<ProductCategory>) {
    const index = this.data.indexOf(node)
    const count = this.findChildrenCount(index, node.level)
    this.data.splice(index + 1, count)
  }

  private async toggleNode(node: DynamicFlatNode<ProductCategory>, expand: boolean) {

    node.isLoading = true

    if (!expand) {
      this.removeChildren(node)
      this.dataChange.next(this.data)
      node.isLoading = false
      return
    }

    const children: Array<ProductCategory>|undefined = await firstValueFrom(this.fetchCategories(node))

    const index = this.data.indexOf(node)

    if (!children || index < 0) {
      // If no children, or cannot find the node, no op
      return
    }

    const nodes = this.treeFlattener.flattenNodes(children)
      .map(childNode => {
        childNode.level += node.level + 1
        return childNode
      })

    // remove existing nodes if exists to be re-added
    // otherwise those will be duplicated
    this.removeChildren(node)

    // add new nodes
    this.data.splice(index + 1, 0, ...nodes)

    this.dataChange.next(this.data)
    node.isLoading = false
  }

  private fetchCategories(node: DynamicFlatNode<ProductCategory>) {
    if (this._cachedCategories[node.item.randomCategoryId]) {
      return of(this._cachedCategories[node.item.randomCategoryId])
    }

    const params = new HttpParams({
      fromObject: {
        randomCategoryId: node.item.randomCategoryId,
        onlyChildrenSubtree: false,
      }
    })

    return this.api.get('filter/categories', MicroserviceType.inv, { params })
      .pipe(map(data => {
        return data.filters['categories']
      }))
      .pipe(tap(children => {
        this._cachedCategories[node.item.randomCategoryId] = children ?? []
      }))
  }

  public loadAllCategories() {
    const params = new HttpParams({ fromObject: { onlyRoot: true } })
    this._loadingAll = true

    this.api.get('filter/categories', MicroserviceType.inv, { params })
      .subscribe((response) => {
        this._hasMore = false
        this._loadingAll = false
        let categories: Array<ProductCategory> = response?.filters?.categories ?? []
        categories = categories.filter((category => category.randomCategoryId !== this.data[0].item.randomCategoryId))
        const nodes = this.treeFlattener.flattenNodes(categories)
          .map(childNode => {
            childNode.level = 0
            return childNode
          })

        this.data.splice(this.data.length, 0, ...nodes)
        this.dataChange.next(this.data)

      })
  }
}
