import { Component, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core'
import { FlatTreeControl } from '@angular/cdk/tree'
import { MatTreeFlattener } from '@angular/material/tree'
import { ProductCategory } from '@core/models/types'
import { takeUntil } from 'rxjs'
import { ApiService } from '@core/services/api.service'
import { ActivatedRoute, Router } from '@angular/router'
import { DynamicFlatNode } from '../../../models/types'
import { DynamicTreeDataSourceService } from '../../../services/dynamic-tree-data-source.service'
import { AutoDestroy } from '@core/common/auto-destroy'

@Component({
  selector: 'app-filter-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.scss']
})
export class CategoryComponent extends AutoDestroy implements OnChanges {

  @Input()
  public options: Array<ProductCategory>

  public activeCategoryId?: string

  private _transformer = (node: ProductCategory, level: number) => {
    return new DynamicFlatNode<ProductCategory>(
      node,
      level,
      node.hasChildren || (!!node.children && node.children.length > 0)
    )
  }

  treeControl = new FlatTreeControl<DynamicFlatNode<ProductCategory>>(
    node => node.level,
    node => node.expandable,
  )

  treeFlattener = new MatTreeFlattener(
    this._transformer,
    node => node.level,
    node => node.expandable,
    node => node.children,
  )

  dataSource = new DynamicTreeDataSourceService(this.treeControl, this.api, this.treeFlattener)

  constructor(private api: ApiService, private router: Router, route: ActivatedRoute) {
    super()
    route.params
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.activeCategoryId = route.snapshot.params['categoryId']
      })
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['options']) {
      this.dataSource.data = this.treeFlattener.flattenNodes(changes['options'].currentValue)
    }
  }

  hasChild = (_: number, node: DynamicFlatNode<ProductCategory>) => node.expandable

  selectFilter(node: DynamicFlatNode<ProductCategory>) {
    this.router.navigate([`${node.item.friendlyUrl}/${node.item.randomCategoryId}`])
  }

  viewAllCategories() {
    this.dataSource.loadAllCategories()
  }
}
