import { ApiService } from '@core/services/api.service'
import { Injectable } from '@angular/core'
import { BehaviorSubject, Observable, takeUntil } from 'rxjs'
import { Product } from '@core/models/product.model'
import { ProductCategory } from '@core/models/types'
import { CategoryFilterOption, CategoryFilters } from '../models/types'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { HttpParams } from '@angular/common/http'

@Injectable({
  providedIn: 'root',
})
export class CategoryListService {

  private _products$ = new BehaviorSubject<Array<Product>>([])
  private _categories$ = new BehaviorSubject<Array<ProductCategory>>([])
  private _totalProducts$ = new BehaviorSubject<number>(0)
  private _activeCategory$ = new BehaviorSubject<ProductCategory|undefined>(undefined)
  private _filters$ = new BehaviorSubject<CategoryFilters>(undefined as any)
  private randomCategoryId?: string

  constructor(private api: ApiService) {}

  init(randomCategoryId: string, filters?: CategoryFilters, pagination?: any) {
    this.randomCategoryId = randomCategoryId
    this.getCategories(randomCategoryId, filters, pagination)
  }

  refetch(pagination?: any, filters?: CategoryFilters) {
    if (! filters) {
      filters = this._filters$.value
    }
    if (this.randomCategoryId) {
      this.getCategories(this.randomCategoryId, filters, pagination)
    }
  }

  get totalProducts$() {
    return this._totalProducts$.asObservable()
  }

  get products$() {
    return this._products$.asObservable()
  }

  get categories$() {
    return this._categories$.asObservable()
  }

  get activeCategory$() {
    return this._activeCategory$.asObservable()
  }

  get filters$() {
    return this._filters$.asObservable()
  }

  set filters(filters: CategoryFilters) {
    this._filters$.next(filters)
  }

  private getCategories(randomCategoryId: string, filters?: any, pagination?: any) {

    const params = new HttpParams({
      fromObject: {
        ...pagination,
        page: pagination['page'] - 1, // pagination starts from 1, whereas api expects it to start from 0
        sort: `id_sort,${pagination['orderBy']}`,
        randomCategoryId: randomCategoryId,
        aggregate: true,
        fetch: true
      }
    })

    this.api.post('filter/category', filters ?? {}, MicroserviceType.inv, { params })
    // this.api.get(`filter/categories/?cid=${randomCategoryId}`)
      .subscribe((data) => {
        // only set categories ones so that when new category is selected
        // loaded category doesn't get destroyed
        if (this._categories$.value.length === 0) {
          this._categories$.next(data.filters.categories ?? [])
        }
        this._products$.next(data.content.content.map(Product.fromDto))
        this._activeCategory$.next(this.findCategory(data.filters.categories ?? []))
        delete data.filters.categories
        this._filters$.next(data.filters)
        this._totalProducts$.next(data.content.totalElements)
      })
  }

  private findCategory(categories: Array<ProductCategory>): ProductCategory | undefined {

    for (let i = 0; i < categories.length; i++) {
      if (categories[i].randomCategoryId === this.randomCategoryId) {
        return categories[i]
      }
      return this.findCategory(categories[i].children || [])
    }
    return undefined
  }

}
