import { Directive, EventEmitter, Injectable, Input, Output } from '@angular/core'
import { FilterOption } from '@core/types'

@Directive()
export class FilterComponent {
  @Input()
  public options: Array<FilterOption>

  @Output()
  public filter = new EventEmitter<Array<number|string>>()

  protected type: 'single' | 'multi' = 'single'

  selectFilter(option: FilterOption, disabled: boolean = false) {
    if (disabled) {
      return
    }
    option.checked = ! option.checked
    this.filter.emit()
  }
}
