import { Component, Inject, OnInit } from '@angular/core'
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog'
import { PaymentMethod } from '@core/models/paymentMethod.model'
import { CategoryFilter } from '@core/types'
import { CategoryListService } from '../../services/category-list.service'
import { CategoryFilters } from '../../models/types'
import { FilterTypes } from '../../services/constants'

@Component({
  selector: 'app-filters-popup',
  templateUrl: './filters-popup.component.html',
  styleUrls: ['./filters-popup.component.scss']
})
export class FiltersPopupComponent {

  public selected: any = {}
  public filters?: CategoryFilters
  public filterTypes = FilterTypes

  constructor(
    private dialogRef: MatDialogRef<FiltersPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { filters: Array<CategoryFilter>, dataService: CategoryListService}
  ) {
    data.dataService.filters$.subscribe(filters => {
      this.filters = JSON.parse(JSON.stringify(filters))
    })
  }

  selectFilter(title: string, type: string, filters: any) {
    this.selected[title] = filters
  }

  save() {
    this.dialogRef.close(this.filters)
  }

  cancel() {
    this.dialogRef.close(null)
  }

  delete() {
    Object.keys(this.filters ?? {}).forEach((key: string) => {
      this.filters?.[key as keyof CategoryFilters].forEach(option => {
        option.checked = false
      })
    })
    this.dialogRef.close(this.filters)
  }
}
