import { Component, EventEmitter, Input, Output } from '@angular/core'
import { FiltersPopupComponent } from '../filters-popup/filters-popup.component'
import { CategoryListService } from '../../services/category-list.service'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { ProductListingType } from '@core/enums/product-listing-type.enum'

@Component({
  selector: 'app-product-filters',
  templateUrl: './product-filters.component.html',
  styleUrls: ['./product-filters.component.scss']
})
export class ProductFiltersComponent {

  @Input()
  public dataSource: CategoryListService

  @Input()
  public queryFilters: any = {}

  @Input()
  public numAppliedFilters = 0

  @Output()
  private productViewUpdated = new EventEmitter

  @Output()
  public filter = new EventEmitter

  readonly listType = ProductListingType

  listing = this.listType.grid
  sizes = [{ value: '20', label: '20' }, { value: '40', label: '40' }, { value: '80', label: '80' }]
  orders = [{ value: 'desc', label: 'Newest' }, { value: 'asc', label: 'Oldest' }]

  constructor(private dialog: MatDialog) {}

  public changePagination() {
    this.dataSource.refetch(this.queryFilters)
  }

  public updateProductView(type: ProductListingType) {
    this.listing = type
    this.productViewUpdated.emit(type)
  }

  showPopup() {
    const dialogRef = this.dialog.open(FiltersPopupComponent, {
      width: '350px',
      data: { dataService: this.dataSource },
    })

    dialogRef.beforeClosed().subscribe((data) => {
      if (data !== null) {
        this.queryFilters.page = 1
        this.dataSource.refetch(this.queryFilters, data)
      }
    })

  }

}
