import { Component, EventEmitter, Input, Output } from '@angular/core'
import { CategoryFilter } from '@core/types'

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.scss']
})
export class FilterComponent {

  @Input()
  public categoryFilter: CategoryFilter|any

  @Input()
  public type: string

  @Input()
  public title: string

  @Output()
  private filter = new EventEmitter<any>()

  selectFilter(filters: any) {
    this.filter.emit(filters)
  }
}
