import { Component } from '@angular/core'
import { FilterOption } from '@core/types'
import { FilterComponent } from '../filter.component'

@Component({
  selector: 'app-filter-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.scss']
})
export class RatingComponent extends FilterComponent {

  override selectFilter(option: FilterOption, disabled: boolean = false) {
    const index = this.options.indexOf(option)
    this.options.slice(0, index)
      .forEach(option => {
        option.checked = !option.checked
      })

    super.selectFilter(option, disabled)
  }
}
