import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core'
import { ProductCategory } from '@core/models/types'
import { CategoryFilters } from '../../models/types'
import { CategoryListService } from '../../services/category-list.service'
import { Router } from '@angular/router'
import { AutoDestroy } from '@core/common/auto-destroy'
import { takeUntil } from 'rxjs'
import { FilterTypes } from '../../services/constants'

@Component({
  selector: 'app-category-filters',
  templateUrl: './category-filters.component.html',
  styleUrls: ['./category-filters.component.scss']
})
export class CategoryFiltersComponent extends AutoDestroy implements OnInit {

  @Input()
  public dataSource: CategoryListService

  @Input()
  public numAppliedFilters = 0

  @Output()
  public filter = new EventEmitter

  public filters?: CategoryFilters

  public filterTypes = FilterTypes

  ngOnInit() {
    this.dataSource?.filters$
      .pipe(takeUntil(this.destroy$))
      .subscribe(filters => {
        this.filters = filters
      })
  }

  public selectedFilters() {
    this.filter.emit()
  }
}
