import { Component, OnInit } from '@angular/core'
import { ProductCategory } from '@core/models/types'
import { Product } from '@core/models/product.model'
import { AutoDestroy } from '@core/common/auto-destroy'
import { ApiService } from '@core/services/api.service'
import { ActivatedRoute, Router } from '@angular/router'
import { CategoryFilters } from '../../models/types'
import { CategoryListService } from '../../services/category-list.service'
import { ProductListingType } from '@core/enums/product-listing-type.enum'

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})
export class CategoryListComponent extends AutoDestroy implements OnInit {

  private randomCategoryId = ''

  initialLoading = false
  menuItems: { slug: string, title: string }[] = []
  breadHeading: string
  breadSubtitle: string
  activeCategory?: ProductCategory
  products: Array<Product> = []
  totalProducts: number
  filters?: CategoryFilters
  categories: Array<ProductCategory> = []
  // private appliedFilters: {[key: string]: Array<number|string>} = {}
  public numAppliedFilters = 0
  public categoryService: CategoryListService
  public queryFilters = {
    size: '20',
    page: 1,
    orderBy: 'desc'
  }

  listType = ProductListingType
  listing: ProductListingType = ProductListingType.grid

  constructor(private api: ApiService, private route: ActivatedRoute, private router: Router) {
    super()
    this.categoryService = new CategoryListService(api)
  }

  ngOnInit() {
    this.route.params.subscribe(() => {
      this.randomCategoryId = this.route.snapshot.params['categoryId'] || this.route.snapshot.queryParams['cid']
      this.fetchData()
      this.fillFilters()
    })

    this.categoryService.activeCategory$.subscribe((category) => {
      this.initialLoading = category === undefined
      this.activeCategory = category
      this.breadHeading = category?.name ?? ''
    })

    this.categoryService.totalProducts$.subscribe((count) => {
      this.totalProducts = count
      this.breadSubtitle = `${count} products found`
    })

    this.categoryService.categories$.subscribe((categories) => {
      this.menuItems = []
      this.createBreadcrumbs(categories)
    })

    this.categoryService.filters$.subscribe((filters) => {
      this.filters = filters
      this.calculateAppliedFilters()
    })
  }

  private fetchData() {
    this.categoryService.init(this.randomCategoryId, this.filters, this.queryFilters)
  }

  public filterApplied() {
    this.queryFilters['page'] = 1
    this.fetchData()
  }

  private fillFilters() {
    const qm = this.route.snapshot.queryParamMap
    const perPage = qm.get('perPage')
    const currPage = qm.get('currPage')
    const orderBy = qm.get('orderBy')
    if (perPage) {
      this.queryFilters.size = perPage
    }
    if (currPage) {
      this.queryFilters.page = +currPage
    }
    if (orderBy) {
      this.queryFilters.orderBy = orderBy
    }
  }

  public gotoPage(page: number) {
    this.queryFilters.page = page
    this.fetchData()
    this.scrollToTop()
  }

  private calculateAppliedFilters() {
    this.numAppliedFilters = Object.keys(this.filters ?? {})
      .filter((v) => {
        const options = this.filters?.[v as keyof CategoryFilters]
        return options?.filter(p => p.checked)?.length
      }).length
  }

  private scrollToTop() {
    window.scroll({
      top: 50,
      left: 0,
      behavior: 'auto'
    })
  }

  private createBreadcrumbs(categories: Array<ProductCategory>) {
    const category = categories[0]

    if (category) {
      this.menuItems.push({ slug: `${category.friendlyUrl}/${category.randomCategoryId}`, title: category.name })
      if (category.randomCategoryId !== this.randomCategoryId && category.children) {
        this.createBreadcrumbs(category.children)
      }
    }

  }

}
